/*_____________________input_____________________*/
.header_search__3lF_p {
    position: relative;
}

.header_inputsearch__3vkzb {
    background-color: #111;
    border: 2px solid #6c6969;
    height: 33px;
    /*width: 30px;*/
    width: 250px;
    padding: 0 15px;
    font-size: 1.1em;
    font-weight: lighter;
    color: white;
    border-radius: 24px;
    transition: all 0.4s;
    transition-timing-function: ease-out;
}

/*.inputsearch:focus {*/
/*    width: 200px;*/
/*    padding: 0 12px;*/
/*    outline:none;*/
/*}*/

.header_inputsearch__3vkzb+.header_search__3lF_p:after {
    content: '';
    border-top: 3px solid #6c6969;
    width: 15px;
    display: block;
    position: absolute;
    right: -6px;
    bottom: -8px;
    transform: rotate(45deg);
    transition: all 0.2s;
    transition-timing-function: ease-out;
}
.header_inputsearch__3vkzb+.header_search__3lF_p:before {
    content: '';
    border-top: 3px solid #6c6969;
    /*width: 0px;*/
    display: block;
    position: absolute;
    right: -2px;
    bottom: 19px;
    transform: rotate(-45deg);
    transition: all 0.2s;
    transition-timing-function: ease-out; /* Safari */
    transition-delay: 0s;
}

.header_inputsearch__3vkzb:focus+.header_search__3lF_p:after {
    content: '';
    border-top: 3px solid #6c6969;
    display: block;
    position: absolute;
    right: 10px;
    bottom: 9px;
    transform: rotate(45deg);
    background-color: #111;
}

.header_inputsearch__3vkzb:focus+.header_search__3lF_p:before {
    right: 10px;
    bottom: 9px;
    width: 15px;
    transition: all 0.1s;
    transition-timing-function: ease-in; /* Safari */
    transition-delay: 0.2s;
}
select {
